.App{

}
.header{
    background: #0ea740;
    padding: 20px;
    text-align: center;
    font-size: 20px;
    color: white;
}
.input-container{
    padding: 20px 40px;
}
.Left,.Right{
    display: inline-block;
    width: 45%;
    border:1px solid #d4dee5;
    padding:20px;
    vertical-align: top;
    background: white;
    border-radius: 4px;
}
.Left p, .Right p{
    text-align: center;
    margin: 0px;
    margin-bottom: 20px;
}
select{
    background: #ffffff;
    color: #333;
    outline: none;
    display: inline-block;
    cursor: pointer;
    width: 40%;
    font-size: 14px;
    border: 1px solid #bcc8d1;
    border-radius: 3px;
    margin-bottom: 10px;
    height: 35px;
    vertical-align: top;
}
.add{
    border: 1px solid #bcc8d1;
    border-radius: 4px;
    padding: 7px 12px;
    display: inline-block;
    margin-left: 10px;
    cursor: pointer;
}
.Right{
    margin-left: 20px;
}
.input{
    margin-left: 10px;
    width: 35%;
    font-size: 14px;
    border: 1px solid #bcc8d1;
    border-radius: 3px;
    padding: 8px;
    vertical-align: top;
}
.repeatInput{
    vertical-align: middle;
    margin-right: 10px;
}
.design-btn{
    padding: 10px;
    border-radius: 4px;
    cursor: pointer;
    margin: 0 auto;
    width: 84px;
    display: block;
    background: #ea3c3c;
    color: white;
}
.design{
    margin-left: 40px;
    margin-top: 20px;
}
.row{
    height: 2px;
}
.col{
	display: inline-block;
	width: 2px;
	height: 2px;
	vertical-align: top;
}
.rowParent{
    display: inline-block;
    vertical-align: top;
}
.outer{
    overflow: scroll;
    width: 1024px;
    margin-left: 30px;
    margin-top: 10px;
}
.design{
    margin-left: 10px;
    width: 10000px;
}
@-webkit-keyframes infinite-color-change {
    0% {
        border: 5px solid maroon;
    }
    25% {
        border: 5px solid red;
    }
    50% {
        border: 5px solid #759E37;
    }
    75% {
        border: 5px solid purple;
    }
    100% {
        border: 5px solid green;
    }
}
@keyframes infinite-color-change {
    0% {
        border: 5px solid maroon;
    }
    25% {
        border: 5px solid red;
    }
    50% {
        border: 5px solid #759E37;
    }
    75% {
        border: 5px solid purple;
    }
    100% {
        border: 5px solid green;
    }
}
.spinner {
   -webkit-animation: infinite-color-change 2s infinite;
           animation: infinite-color-change 2s infinite;
   width: 25px;
   height: 25px;
   border-radius: 50%;
   margin: 0 auto;
   margin-top: 20px;
   margin-bottom: 10px;
}
@media (max-width: 600px) {
    .input-container{
        padding: 20px 10px;
    }
    .Left,.Right{
        width: 88%;
    }
    .outer{
        margin-left: 0px;
    }
    .Right{
        margin-left: 0px;
        margin-top: 10px;
    }

}



